/*--溢出文字點點點--*/
@mixin over_text($line) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
}

/*--隱藏區塊內文字--*/
@mixin text-indent {
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
}
/* --------------按鈕設定開始--------------- */
@mixin btn-dark($bgcolor) {
    color: $white !important;
    background: $bgcolor !important;
    border: 1px solid $bgcolor;
    padding: 4px 16px;
    border-radius: $w-xxs;
    font-size: $font-sm;
    @include Pad{
        font-size: $font-xsm;
    }
    &:hover {
        color: $white !important;
        filter: brightness(80%) contrast(180%);
    }
    img {
        margin-right: 4px;
    }
}
@mixin btn-border($color) {
    color: $color !important;
    background: $white !important;
    border: 2px solid $gray300;
    border-radius: $w-xxs;
    position: relative;
    padding: 3px $w-sm;
    font-size: $font-xs;
    img {
        margin-right: $w-xxxs;
    }
    &:hover {
        color: $white;
        &::after{
            transform: scale(1);
            opacity: 1;
        }
    }
    &::after {
        content: "";
        box-sizing: border-box;
        position: absolute;
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        left: -2px;
        top: -2px;
        border: 2px solid $color;
        border-radius: $w-xxs;
        transform: scale(1.2);
        opacity: 0;
    }
}
