body {
	font-family: Noto Sans TC, 微軟正黑體;
}
/*--------------------主選單開始------------------*/
.navbar {
	background: $primary-light-color;
	align-items: center;
	color: $white;
	&-brand {
		width: 100%;
		max-width: 620px;
		@include PC-md {
			max-width: 450px;
		}
		@media mobile-md {
			max-width: 300px;
		}
	}
	&-collapse {
		position: absolute;
		width: 100%;
		left: 0;
		top: calc(100% + 8px);
		@include PC-sm {
			flex-shrink: 1;
		}
		@include Pad {
			background: $primary-color;
			position: absolute;
			left: 0;
			width: 100%;
		}
	}
	&-nav {
		display: flex;
		flex-direction: row;
		flex-shrink: 0;
		padding: 0 0 0 $w-xxs;
		margin-left: auto;
	}
}
/*-----------------右側選單開始------------------*/
.nav {
	&-item {
		flex-shrink: 0;
		display: flex;
		align-items: center;
		@include PC {
			font-size: $font-xs;
		}
	}
	&-link {
		border-radius: $w-xxxs;
		border: 1px solid $white;
		padding: $w-xxxs $w-sm !important;
		font-size: $font-xsm;
		color: $white;
		&:hover {
			color: $white;
			background: $primary-xlight-color;
			border: 1px solid $primary-xlight-color;
		}
		@include PC-sm {
			font-size: $font-xs;
		}
		@include Pad {
			font-size: $font-xxs;
		}
	}
}
/*------------------------測邊選單開始----------------------------*/
.side-menu {
	width: 330px;
	height: calc(100vh - 53px);
	position: fixed;
	left: 0;
	top: 53px;
	transition: all 0.2s ease;
	z-index: 200;
	background: $white;
	box-shadow: 0 10px 10px $gray600;
	padding-top: $w-lg;
	@include PC-lg {
		width: 280px;
		padding-top: $w-md;
	}
	@include Pad {
		top: 81px;
		min-height: calc(100vh - 81px);
	}
	@include mobile-md {
		top: 70px;
		min-height: calc(100vh - 70px);
	}
	&-body {
		height: 100%;
	}
	&-toggler {
		display: flex;
		padding: 0;
		width: 28px;
		height: 65px;
		background: $secondary-color;
		border-radius: 0 8px 8px 0 !important;
		position: absolute;
		right: -28px;
		top: $w-sm;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		z-index: 10;
		@include Pad {
			width: 24px;
			right: -24px;
			height: 56px;
			top: 130px;
		}
		&:hover {
			background: $primary-xlight-color;
		}
		img {
			width: $w-xs;
			transform: rotate(0deg);
			@include Pad {
				width: $w-xxs;
			}
		}
	}
	&.close {
		left: -330px;
		@include PC-lg {
			left: -280px;
		}
		.side-menu-toggler {
			img {
				transform: rotate(180deg);
			}
		}
	}
	.accordion-item {
		border-radius: 0 !important;
		border: 0 !important;
	}
	.accordion-button {
		font-size: $font-md;
		padding: $w-sm 30px $w-sm $w-sm;
		color: $primary-light-color;
		border-radius: 0 !important;
		position: relative;
		justify-content: center;
		text-align: center;
		@include PC-lg {
			padding: $w-sm 24px $w-sm $w-sm;
			font-size: $font-smd;
		}
		&::after {
			display: none;
		}
		&:focus {
			box-shadow: none;
		}
		&:not(.collapsed) {
			font-weight: $font-bold;
			color: $primary-color;
			background: $white;
			box-shadow: none;
			&::before {
				content: "";
				display: block;
				width: calc(100% - 32px);
				height: 2px;
				background: $primary-light-color;
				position: absolute;
				left: 16px;
				bottom: 0;
			}
			img {
				filter: invert(49%) sepia(33%) saturate(423%) hue-rotate(340deg) brightness(94%) contrast(95%);
			}
		}
	}
	.accordion-body {
		background: $secondary-light-color;
		border-radius: 0 0 5px 5px;
		padding: $w-lg 13%;
		font-size: $font-smd;
		@include PC-lg {
			padding: $w-md 10%;
			font-size: $font-sm;
		}
	}
	.accordion-link {
		display: block;
		color: $gray800;
		position: relative;
		padding: $w-xxs $w-sm $w-xxs $w-md;
		&::before {
			content: "";
			display: block;
			position: absolute;
			left: 0;
			top: 16px;
			width: 6px;
			height: 11px;
			background: $primary-color;
		}
		&:hover {
			color: $primary-color;
			&:before {
				background: $special-color;
			}
		}
	}
}
/*------------------------內容區塊開始----------------------------*/
.side-content {
	width: calc(100% - 330px);
	margin-left: 330px;
	margin-top: 61px;
	transition: all 0.2s ease;
	padding: $w-lg 7%;
	min-height: calc(100vh - 61px);
	@include PC-lg {
		padding: $w-lg 5%;
		width: calc(100% - 280px);
		margin-left: 280px;
	}
	@include PC-md{
		height: calc(100vh - 51px);
		margin-top: 51px;
	}
	@include Pad {
		width: 100%;
		margin-left: 0;
		margin-top: 70px;
	}
	@include mobile-lg{
		margin-top: 60px;
	}
	&.close {
		width: 100%;
		margin-left: 0;
	}
	&-header {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include Pad {
			flex-direction: column-reverse;
			align-items: start;
		}
	}
}
.content-box {
	background: $white;
	border-radius: $w-xs;
	&[data-con="edit"] {
		border-radius: 0 0 $w-xs $w-xs;
		.form-select,
		.form-control {
			background-color: $white;
		}
		.btn {
			height: 38px;
		}
		.col-form-label {
			@include PC-md {
				width: 100px;
			}
			@include Pad {
				width: 95px !important;
			}
		}
	}
	&-nav {
		background: $primary-xxlight-color;
		border-radius: $w-xs $w-xs 0 0;
		padding: $w-xxs $w-xxs 0 $w-xxs;
	}
}
.content-edit{
		border-radius: 0 0 $w-xs $w-xs;
		.form-select,
		.form-control {
			background-color: $white;
		}
		.btn {
			height: 38px;
		}
		.col-form-label {
			@include PC-md {
				width: 100px;
			}
			@include Pad {
				width: 95px !important;
			}
		}
	}
/*------------------地圖區塊開始--------------------*/
.iframe-map-height{
	height: calc(100vh - 61px);
	@include PC-md{
		height: calc(100vh - 51px);
	}
	@include Pad {
		height: calc(100vh - 70px);
	}
	@include mobile-lg{
		height: calc(100vh - 60px);
		flex-direction: column;
	}
}

/*--------------------footer開始------------------*/
footer {
	margin-top: 80px;
}
.footer {
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	color: $white;
	font-size: $font-xsm;
	font-weight: $font-light;
	@include PC {
		flex-direction: column;
	}
	@include PC-md {
		font-size: $font-xs;
	}
	@include Pad {
		font-size: $font-xxs;
		flex-direction: column;
	}
	&-logo {
		width: 210px;
		@include PC {
			width: 180px;
		}
		@include Pad {
			width: 150px;
		}
	}
	&-nav {
		display: flex;
	}
	&-item {
		&:last-child {
			.footer-link {
				border-right: 0;
			}
		}
	}
	&-link {
		color: $white !important;
		padding: 0 $w-sm;
		border-right: 1px solid $white;
		&:hover {
			opacity: 0.8;
		}
	}
	&-bottom {
		width: 100%;
		flex-shrink: 0;
		display: flex;
		justify-content: space-between;
		text-align: center;
		font-size: $font-xs;
		@include Pad {
			font-size: $font-xxs;
		}
		@include PC-sm {
			align-items: center;
			flex-direction: column;
		}
		p:last-child {
			font-size: $font-xxxs;
		}
	}
}
.gotop {
	position: fixed;
	right: calc(50% - 512px);
	bottom: 170px;
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: $white;
	border-radius: 10px;
	opacity: 0;
	border: 1px solid $primary-color;
	@include PC-sm {
		width: 44px;
		height: 44px;
		line-height: 44px;
		font-size: $font-xxs;
		bottom: 68px;
	}

	&:hover {
		color: $white;
		background: $alert-color;
		img {
			filter: invert(100%) sepia(0%) saturate(7469%) hue-rotate(68deg) brightness(500%) contrast(97%);
		}
	}

	&.show {
		opacity: 1;
	}
}
/*--------------------視窗開始------------------*/
.modal {
	padding-top: 100px;
	&-content {
		overflow: hidden;
	}
	&-header {
		border: 0;
		color: $white;
		display: flex;
		justify-content: center;
		position: relative;
		border-bottom: 1px solid $gray400;
		button {
			position: absolute;
			right: 0;
			top: 12px;
			background-size: 12px;
			&:focus,
			&:active {
				border: none !important;
			}
		}
	}
	&-body {
		background: $white;
		font-size: $font-sm;
		min-height: 100px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	&-footer {
		background: $white;
		border: 0;
		display: flex;
		justify-content: center;
	}
	&-action {
		border-radius: $w-xxxs;
		letter-spacing: 2px;
		max-width: 200px;
	}
}
.modal {
	display: none;
	opacity: 0;
	transition: opacity 0.3s ease;
}
.modal.fade.show {
	display: block;
	opacity: 1;
}
.modal-backdrop {
	background-color: rgba(0, 0, 0, 0.3); /* 設定背景顏色及透明度 */
}
.section {
	padding-top: 64px;
	font-size: $font-sm;
	@include PC-lg {
		padding-top: 32px;
	}
	@include Pad {
		font-size: $font-sm;
	}
	&-container {
		background: $white;
		border: 1px solid $gray200;
		padding: $w-md $w-xl;
		position: relative;
		@include Pad {
			padding: $w-md $w-lg;
		}
		&-id {
			position: absolute;
			width: 30px;
			height: 30px;
			display: flex;
			justify-content: center;
			align-items: center;
			background: $gray300;
			color: $white;
			border-radius: 0 $w-xxxs $w-xxxs 0;
			left: 0;
			top: $w-md;
			@include Pad {
				width: 25px;
				height: 25px;
			}
		}
	}
}

.no-file {
	background: $gray100;
	border: 1px dashed $gray500;
	color: $gray600;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 80px;
	@include Pad {
		font-size: $font-sm;
	}
}
.file-list {
	background: $gray100;
	border-radius: $w-xxxs;
	padding: $w-sm;
	&-item {
		display: flex;
		align-items: center;
		border-bottom: 3px solid $white;
		padding: 4px 0;
		@include Pad {
			flex-wrap: wrap;
			justify-content: center;
		}
		&:last-child {
			border: none;
		}
		&:hover {
			background: $white;
		}
	}
	&-info {
		border-right: 1px solid $gray500;
		font-size: $font-sm;
		width: 60px;
		text-align: center;
		padding: 0 $w-sm 0 $w-xs;
		@include Pad {
			width: 100%;
			border: 0;
		}
	}
	&-txt {
		padding-left: $w-sm;
		@include Pad {
			width: 100%;
			flex-shrink: 0;
			padding-left: 0;
			font-size: $font-sm;
		}
	}
}
