/*--------------------登入頁開始------------------*/
.login {
  display: flex;
  min-height: 100vh;
  align-items: stretch;
  background: $primary-color;
  @include PC {
    flex-direction: column;
  }
  &-vision {
    width: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../images/login-bg.svg") no-repeat right top $primary-color;
    background-size: 100% contain;
    @include PC {
      width: 100%;
      background: url("../images/login-bg-mobile.svg") no-repeat left bottom $primary-color;
      background-size: 100%;
      padding: 40px 0 60px 0;
    }
    @include Pad {
      padding: 30px 0 50px 0;
    }
    @include mobile-lg {
      padding: 20px 0 40px 0;
    }
    @include mobile-md{
      padding: 15px 0 30px 0;
    }
    img {
      width: 80%;
      max-width: 800px;
      @include PC {
        max-width: 350px;
      }
      @include PC-sm {
        max-width: 300px;
      }
      @include Pad {
        width: 45%;
        max-width: 250px;
      }
    }
  }
  &-form {
    width: 45%;
    max-width: 600px;
    margin: 0 auto;
    @include PC-lg {
      padding: 0 70px;
    }
    @include PC {
      width: 100%;
      padding: 50px 5%;
    }
    @include PC-sm {
      padding: 40px 8%;
    }
    @include Pad {
      padding: 30px 10%;
    }
    @include mobile-md{
      padding: 20px 10%;
    }
  }
  &-title {
    font-size: $font-xxxl;
    letter-spacing: 3px;
    color: $white;
    text-align: center;
    @include PC-lg {
      letter-spacing: 0;
      font-size: $font-xxl;
    }
    @include Pad {
      font-size: $font-xl;
    }
    span {
      font-size: $font-xl;
      font-weight: $font-light;
      @include PC-lg {
        font-size: $font-lg;
      }
      @include mobile-lg {
        font-size: $font-md;
      }
    }
  }

  &-verify {
    display: flex;
    flex-direction: column;
    input {
      margin-right: 8px;
      font-size: $font-md;
    }
    label {
      width: 100%;
      font-size: $font-smd;
    }
  }
  &-btn {
    border-radius: 10px !important;
    height: 60px;
    font-size: $font-lg;
    @include PC-lg {
      height: auto;
      height: 55px;
    }
    @include Pad {
      font-size: $font-md;
      height: 50px;
    }
    @include mobile-lg {
      font-size: $font-smd;
      height: 40px;
    }
  }
  input {
    border-radius: 10px;
    border: none;
    background: $white;
    padding-left: 72px;
    height: 55px;
    font-size: $font-md;
    @include PC-md {
      padding-left: 50px;
      font-size: $font-smd;
    }
    @include PC {
      height: 50px;
    }
    @include Pad {
      height: 40px;
      font-size: $font-sm;
    }
    &[data-con="account"] {
      background: url("../images/ic-account.svg") no-repeat left center $white;
      background-size: 50px;
      @include PC-md {
        background-size: 40px;
      }
    }
    &[data-con="password"] {
      background: url("../images/ic-password.svg") no-repeat left center $white;
      background-size: 50px;
      @include PC-md {
        background-size: 40px;
      }
    }
    &[data-con="captcha"] {
      background: url("../images/ic-captcha.svg") no-repeat left center $white;
      background-size: 50px;
      @include PC-md {
        background-size: 40px;
      }
    }
    &:focus {
      box-shadow: none;
    }
    &::placeholder {
      font-size: $font-md;
      @include PC-md {
        font-size: $font-smd;
      }
      @include Pad {
        font-size: $font-sm;
      }
    }
  }
  &-function {
    display: block;
    width: 25px;
    @include PC-sm {
      width: 20px;
    }
    @include Pad {
      width: 68px;
      display: flex;
      justify-content: space-between;
    }
    &-item {
      width: 100%;
      @include Pad {
        width: 30px;
      }
    }
    &-link {
      width: 100%;
      height: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      @include PC-sm {
        height: 25px;
      }
      @include Pad {
        width: 30px;
        height: 45px;
      }
      img {
        width: 100%;
        @include Pad {
          width: 30px;
        }
      }
    }
  }
  &-brand {
    display: flex;
    justify-content: center;
    align-items: center;
    color: $secondary-color;
    @include PC {
      font-size: $font-xsm;
    }
    @include Pad {
      font-size: $font-xs;
    }
    @include mobile-lg{
      font-size: $font-xxs;
    }
    img {
      width: 30px;
      margin-right: $w-xs;
      @include PC {
        width: 25px;
      }
      @include mobile-lg{
      width: 20px;
    }
    }
  }
}
.captcha {
  height: 55px;
  @include PC {
    height: 50px;
  }
  @include mobile-lg{
    height: 40px;
  }
}
